/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
    color: #222;
    font-size: 1em;
    line-height: 1.4;
}

/*
 * Remove text-shadow in selection highlight:
 * https://twitter.com/miketaylr/status/12228805301
 *
 * These selection rule sets have to be separate.
 * Customize the background color to match your design.
 */

::-moz-selection {
    background: #b3d4fc;
    text-shadow: none;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

/*
 * A better looking default horizontal rule
 */

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

/*
 * Remove default fieldset styles.
 */

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */

textarea {
    resize: vertical;
}

/* ==========================================================================
   Browser Upgrade Prompt
   ========================================================================== */

.browserupgrade {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}

/* ==========================================================================
   Custom styles
   ========================================================================== */



html, body {
   height: 100%;
   width: 100%;
   margin: 0;
   padding: 0;
}



body {
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  line-height: 1.25;
  color: white;
  display:flex; 
  flex-direction:column; 
}

h1 {
 display: flex;
 flex-direction: column;
 align-items: center;
 margin: 10px 0 0 0;
 font-weight: 800;
}

a, a:active, a:hover, a:visited {
  color: #555;
  transition: all ease-out 0.1s;
 text-decoration-color: DeepSkyBlue;
}

a:hover {
  box-shadow: inset 0 -10px 0 DeepSkyBlue;
  transition: all ease-out 0.3s;
}

.default {
  color: white;
}

.default-bg {
 background: linear-gradient(lime, green);
}

.moderate {
 background: linear-gradient(gold, yellowgreen);
}


.unhealthySensitive {
 background: linear-gradient(gold, goldenrod);
}

.unhealthy {
 background: linear-gradient(red, crimson);
}

.very-unhealthy {
 background: linear-gradient(darkorchid, darkviolet);
}

.hazardous {
 background: linear-gradient(indigo, darkslateblue);
}


.high-pollution {
  color: tomato;
}

.descriptive {
 font-size: 3rem;
 margin: 14px 0 0 0;
}


#avg25, #rtreadings, #card-container {
 padding: 10px 0 0 0; 
 display: flex;
 flex-direction: column;
 align-items: center;
 
}

#rtreadings {
 text-decoration: underline;
 font-size: 1.2rem;
 font-weight: bold;
}

#avg25value {
 font-size: 7rem;
 margin: 0 0 -20px 0;
 font-weight: 800;
}

#pollution {
 padding: 10px 0 0 0;
 display: flex;
 flex-direction: row;
 justify-content: center;
 align-items: center;
}

#pm1value, #pm25value, #pm10value {
 font-size: 2rem;
 padding: 0 10px;
 font-weight: bold;
}


#card-container {
 margin: 40px 0 0 0;
 color: #555;
 font-size: .95rem;
 font-weight: 300;
}



#card {
 width: 320px;
 padding: 20px;
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
 transition: 0.3s;
 background-color: white;
}

#card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

ul { 
   list-style-type: square;
   list-style-position: inside;
   margin-left: -40px;
}







/* ==========================================================================
   Helper classes
   ========================================================================== */

/*
 * Hide visually and from screen readers
 */

.hidden {
    display: none !important;
}



/*
 * Hide visually and from screen readers, but maintain layout
 */

.invisible {
    visibility: hidden;
}

/*
 * Clearfix: contain floats
 *
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    `contenteditable` attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that receive the `clearfix` class.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */

.clearfix:before,
.clearfix:after {
    content: " "; /* 1 */
    display: table; /* 2 */
}

.clearfix:after {
    clear: both;
}

/* ==========================================================================
   EXAMPLE Media Queries for Responsive Design.
   These examples override the primary ('mobile first') styles.
   Modify as content requires.
   ========================================================================== */

@media only screen and (min-width: 35em) {
    /* Style adjustments for viewports that meet the condition */
}

@media print,
       (-webkit-min-device-pixel-ratio: 1.25),
       (min-resolution: 1.25dppx),
       (min-resolution: 120dpi) {
    /* Style adjustments for high resolution devices */
}

/* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request:
   http://www.phpied.com/delay-loading-your-print-css/
   ========================================================================== */

@media print {
    *,
    *:before,
    *:after,
    *:first-letter,
    *:first-line {
        background: transparent !important;
        color: #000 !important; /* Black prints faster:
                                   http://www.sanbeiji.com/archives/953 */
        box-shadow: none !important;
        text-shadow: none !important;
    }

    a,
    a:visited {
        text-decoration: underline;
    }

    a[href]:after {
        content: " (" attr(href) ")";
    }

    abbr[title]:after {
        content: " (" attr(title) ")";
    }

    /*
     * Don't show links that are fragment identifiers,
     * or use the `javascript:` pseudo protocol
     */

    a[href^="#"]:after,
    a[href^="javascript:"]:after {
        content: "";
    }

    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }

    /*
     * Printing Tables:
     * http://css-discuss.incutio.com/wiki/Printing_Tables
     */

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    img {
        max-width: 100% !important;
    }

    p,
    h2,
    h3 {
        orphans: 3;
        widows: 3;
    }

    h2,
    h3 {
        page-break-after: avoid;
    }
}
